body {

}

.App {
  background-color: #f5f3f5;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.root {
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 667;
  z-index: 1;
  overflow: hidden;
  position: relative;
  display: -ms-flexbox;
  display: flex;
}

.root-body {
  width: 100%;
  min-height: 100vh;
  margin-top: 56px;
  padding: 40px 25px;
  background: #f1f1f1;
}



